@import "import/reset";
.wrapper {
    max-width: 1441px;
    margin: 0 auto;
    font-family: "Open Sans"; }

.swiper-container {
    width: 100%; }

.icon {
    width: 100%;
    height: 100%;
    &-map {
        width: 21px;
        height: 18px; }
    &-logoicon {
        width: 55px;
        height: 67px; }
    &-workonghoursicon {
        min-width: 24px;
        min-height: 24px;
        width: 24px;
        height: 24px; }
    &-contactviber2, &-contactwatsapp2, &-contacttelegram2 {
        width: 25px;
        height: 27px; }
    &-phoneicon {
        width: 17px;
        height: 26px;
        transform: rotate(15deg); }
    &-repostbuttonicon {
        width: 21px;
        height: 16px; }
    &-gears {
        width: 24px;
        height: 17px; }
    &-contactviber1, &-contactwatsapp1, &-contacttelegram1, &-contactviber3, &-contactwatsapp3, &-contacttelegram3 {
        width: 16px;
        height: 17px; }
    &-checked {
        width: 57px;
        height: 56px;
        min-width: 57px;
        min-height: 56px; } }
.swiper-button-next {
    background-image: url(../img/arrowright.webp);
    background-repeat: no-repeat;
    background-size: contain;
    width: 14px;
    height: 14px;
    &::after {
        display: none; } }
.swiper-button-prev {
    background-image: url(../img/arrowleft.webp);
    background-repeat: no-repeat;
    background-size: contain;
    width: 14px;
    height: 14px;
    &::after {
        display: none; } }


.icon-button {
    button {
            background-color: #333333;
            display: flex;
            align-items: center;
            padding-left: 19px;
