            border-bottom: 3px solid transparent;
            &:hover {
                text-decoration: none;
                color: #fff;
                border-color: #ff0000; } } }

    &__mobile {
        visibility: hidden;
        pointer-events: none;
        position: absolute;
        left: 0px;
        top: 62px;
        height: 0px;
        display: flex;
        overflow: hidden;
        width: 100%;
        background: rgba(0, 0, 0, 0.932);
        z-index: 25;
        flex-direction: column;
        transition: height 0.5s linear, visibility 1s linear;
        &-item {
            padding-top: 5px;
            padding-bottom: 5px;
            a {
                text-decoration: none;
                font-size: 14px;
                color: #ffffff;
                font-weight: 400;
                font-family: Open Sans;
                margin-left: 5px;
                white-space: nowrap;
