        &-bottom {
            display: flex;
            height: 100%;
            align-items: center;
            padding-top: 80px;
            flex-direction: column; }
        &_visible {
            height: 500px;
            visibility: visible;
            pointer-events: auto; } }

    &__mobile-button {
        display: none;
        padding: 10px 20px;
        cursor: pointer;
        outline: none;
        border: none;
        align-items: center;
        background: none;
        margin-left: auto;
        @media(max-width: 785px ) {
            display: flex; }
        i {
            display: block;
            width: 23px;
            height: 3px;
            margin: 6px 0 6px 9px;
            position: relative;
            &::before {
                content: "";
                width: 100%;
                height: 3px;
                background: #fff;
                position: absolute;
                right: 0px;
                top: -9px;
                display: block; }
            span {
                width: 50%;
                height: 100%;
                background: #fff;
                position: absolute;
                right: 0px;
                display: block;
                transition: all 0.4s linear; }
            &::after {
                content: "";
                width: 70%;
                height: 3px;
                right: 0px;
                bottom: -9px;
                background: #fff;
                position: absolute;
                display: block;
                transition: all 0.4s linear; } }
        &:hover {
            i {
                span {
                    width: 100%; }
                &::after {
                    width: 100%; } } } } }
